import { NextResponse } from "next/server"
import fs from "fs"
import path from "path"

const dataFilePath = path.join(process.cwd(), "src/data/blog.json")

export async function GET() {
    try {
        const fileContents = fs.readFileSync(dataFilePath, "utf8")
        const data = JSON.parse(fileContents)
        return NextResponse.json(data.posts)
    } catch (error) {
        return NextResponse.json({ error: "Failed to read data" }, { status: 500 })
    }
}

export async function POST(request: Request) {
    try {
        const newPosts = await request.json()

        // Ensure we are writing the full structure { posts: [...] }
        const fileContent = JSON.stringify({ posts: newPosts }, null, 2)

        fs.writeFileSync(dataFilePath, fileContent, "utf8")

        return NextResponse.json({ success: true })
    } catch (error) {
        return NextResponse.json({ error: "Failed to save data" }, { status: 500 })
    }
}
